import UniPopup from '@/third/uni-popup/uni-popup.vue';
export default {
	name: 'stat-date-picker',
	components: {
		UniPopup
	},
	props: {
		value: {
			type: Boolean,
			default: false
		},
		title: {
			type: String,
			default: '选择时间'
		},
		startDate: {
			type: String,
			default: () => {
				return new Date().toDateString();
			}
		},
		endDate: {
			type: String,
			default: () => {
				return new Date().toDateString();
			}
		},
		//显示模式
		type: {
			type: Number,
			default: 2
		}
	},
	data() {
		return {
			showPopup: false,
			years: [], //保存年份
			months: [], //保存月份
			defaultValue: [0, 0, 0], //默认选中项
			tabFlag: 'Y'
		};
	},
	created() {
		let startDateYear = new Date(this.startDate).getFullYear();
		let endDateYear = new Date(this.endDate).getFullYear();
		let curMonth = new Date(this.endDate).getMonth() + 1;
		for (let i = startDateYear; i <= endDateYear; i++) {
			this.years.push(i);
		}
		for (let i = 1; i <= 12; i++) {
			this.months.push(i);
		}
		this.years.forEach((item, index) => {
			if (endDateYear === item) {
				this.defaultValue[0] = index;
			}
		});
		this.months.forEach((item, index) => {
			if (curMonth === item) {
				this.defaultValue[1] = index;
			}
		});
	},
	computed: {
		model: {
			get() {
				return this.value;
			},
			set(n) {
				this.$emit('input', n);
			}
		}
	},
	watch: {
		value(n) {
			this.showPopup = n;
			this.defaultValue = this.defaultValue;
		}
	},

	methods: {
		//确认事件
		confirm() {
			//获取索引位置
			let yearsIndex = this.defaultValue[0];
			let monthsIndex = this.defaultValue[1];
			//从数组中取值
			let selectedDate = {
				year: this.years[yearsIndex],
				month: this.months[monthsIndex],
				type: this.tabFlag
			};
			this.showPopup = false;
			this.$emit('input', false);
			//发射数据
			this.$emit('changeDate', selectedDate);
		},
		//选项卡 切换事件
		changeTab(e) {
			this.tabFlag = e;
		},
		//pikcer change事件
		pickerChange({ detail }) {
			if (detail.value[0] === 0) {
				return;
			}
			this.defaultValue = detail.value;
		},
		//取消事件
		cancel() {
			this.showPopup = false;
			this.$emit('input', false);
		},
		//popup关闭事件
		closeChange(e) {
			this.$emit('input', e);
		}
	}
};
